.footer {
  background-color: var(---cor-textobk);
  color: var(---cor-texto);
  font-family: "Fredoka One",
    cursive;

  width: 100%;
  height: 60px;

  display: flex;
  align-items: center;
  align-content: center;
  justify-content: center;
  padding: 5px;
}

.footer a {
  color: var(---link-color);

}

.footer a:hover {
  color: var(---destaque-hover);

}